img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 31px;
  }
}
.page {
  height: 100%;
  font-family: "ptsans", "Arial", sans-serif;
  font-weight: 400;
}
.page__body {
  height: 100%;
  margin: 0;
}
@media (min-width: 1200px) {
  .page__body {
    background-color: #E7E5E5;
  }
}
.page__wrapper {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
@media (min-width: 1200px) {
  .page__wrapper {
    width: 1200px;
    margin: 0 auto;
    background-color: #FFFFFF;
    box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.page-footer {
  height: 300px;
  background-color: tomato;
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: 16px;
  right: 18px;
  width: 23px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
}
.main-nav--closed .main-nav__toggle:hover,
.main-nav--closed .main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -1px;
  width: 26px;
  height: 4px;
  background-color: #FFFFFF;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover,
.main-nav--opened .main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.main-nav__item {
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
.main-nav__item:first-child {
  border-top: none;
}
.main-nav__item:nth-child(4) {
  border-bottom: none;
}
.main-nav__item:nth-last-child(2) {
  position: relative;
}
.main-nav__item:nth-last-child(2)::after {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 20px;
  background: linear-gradient(to top, #678fa8 0, transparent 10px);
}
@media (min-width: 768px) {
  .main-nav__item:nth-last-child(2)::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    width: 20%;
    border: none;
  }
  .main-nav__item:nth-child(1) {
    order: 1;
  }
  .main-nav__item:nth-child(2) {
    order: 2;
  }
  .main-nav__item:nth-child(3) {
    order: 4;
  }
  .main-nav__item:nth-child(4) {
    order: 5;
  }
  .main-nav__item:nth-child(5) {
    order: 3;
  }
}
.main-nav--closed .main-nav__item {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__item {
    display: block;
  }
}
.main-nav__item--logo {
  height: 56px;
  border: none;
}
.main-nav--closed .main-nav__item--logo {
  display: block;
}
.main-nav__link {
  display: block;
  padding: 19px 15px 12px;
  background-color: #81B3D2;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}
.main-nav__link:hover,
.main-nav__link:focus {
  background-color: #669EC0;
}
.main-nav__link:active {
  background-color: #5496BD;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 15px 15px 15px;
    font-size: 14px;
    line-height: 26px;
    color: black;
    background: none;
  }
  .main-nav__link:hover,
  .main-nav__link:focus {
    background: none;
    color: #81B3D2;
  }
  .main-nav__link:active {
    background: none;
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__link-current {
    color: #766357;
  }
  .main-nav__link-current:hover,
  .main-nav__link-current:focus {
    color: #604E43;
  }
  .main-nav__link-current:active {
    background: none;
    color: rgba(118, 99, 87, 0.3);
  }
}
.main-nav__item--logo {
  text-align: center;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
